import { Component, useState, useEffect } from "react";
import {IconButton,TextField, Box, Tooltip} from "@mui/material";
import SearchIcon from "@mui/icons-material/Search";
import SatelliteAltIcon from '@mui/icons-material/Satellite';
import CancelIcon from '@mui/icons-material/Cancel';
import TitleIcon from '@mui/icons-material/Title';
import HailIcon from '@mui/icons-material/Hail';

import Checkbox from '@mui/material/Checkbox';
import FormGroup from '@mui/material/FormGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';
import FormLabel from '@mui/material/FormLabel';
import {Input,Row,Col} from "reactstrap";
import "bootstrap/dist/css/bootstrap.css";
import './application.css'
import { DataGrid, GridColDef, GridValueGetterParams } from '@mui/x-data-grid';
import AddNewUrl from './AddNewUrl';


function Home(props) {

  const [searchQuery, setSearchQuery] = useState("");

    const [soeId, setSoeId] = useState("Y");
    const [myLinks, setMyLinks] = useState("Y");
    const [userData, setUserData] = useState([]);
    const [publicLinks, setPublicLinks] = useState("N");
    const [followingLinks, setFollowingLinks] = useState("N");

    const [searchedData, setSearchedData] = useState([]);

    const loginStatus = props.authenticationStatus;

    useEffect(() => {
      setUserData(props.data);
      setSoeId(props.data.SoeId);
      setMyLinks(true); 
      setPublicLinks(false); 
      setFollowingLinks(false); 
      handleUserDataSearch(); 
  },[props]);


  const handleUserDataSearch = async e => {
       await userDataSearch({
            soeId,publicLinks,followingLinks,myLinks
        });
      }

      async function userDataSearch(credentials){
	    try {
          fetch('/snaplink/api/searchForUserLinks',{
            method:'POST',
            headers:{
                'Content-Type':'application/json'
            },
            body: JSON.stringify(credentials)
              }).then((response) => response.json())
                .then((responseData) => {
                  setSearchedData(responseData.userLinks);
                    });
      } catch (error) {
           }
    }

  const recommMultipleActions = (params) => {
    return (
      <strong>
         <Tooltip title="Close Recommendation" arrow>
         <IconButton
                edge="end"
                aria-label="reject"
                onClick={() => {
                  
                }}
            >
                <CancelIcon/>
         </IconButton>
         </Tooltip>
         <Tooltip title="MyIdeas" arrow>
         <IconButton
                edge="end"
                aria-label="myideas"
                onClick={() => {
               
                }}
            >
                <HailIcon/>
         </IconButton>
         </Tooltip>
         <Tooltip title="Same Symbol Ideas" arrow>
         <IconButton
                edge="end"
                aria-label="similarideas"
                onClick={() => {
               
                }}
            >
                <SatelliteAltIcon/>
         </IconButton>
         </Tooltip>
      </strong>
  )
   } 

  const columns: GridColDef[] = [
    { field: 'userLinksId', headerName: 'ID', width: 90 },
    {
      field: '',
      headerName: '',
      width: 135,
      renderCell: recommMultipleActions,
    },
    {
      field: 'url',
      headerName: 'URL',
      width: 250,
    },
    {
      field: 'userLinkDescription',
      headerName: 'Description',
      width: 150,
    },
    {
      field: 'type',
      headerName: 'Type',
      width: 150,
    },
    {
      field: 'userLinkTags',
      headerName: 'Tags',
      width: 150,
    },
    {
      field: 'soeId',
      headerName: 'Owner',
      width: 150,
    },
    {
      field: 'createDate',
      headerName: 'Create Date',
      width: 110,
    },
    {
      field: 'lastAccessDate',
      headerName: 'Last Access Date',
      width: 110,
      
    }
  ];

 
  const filterData = (query, data) => {
    if (!query) {
      return data;
    } else {
      return data.filter((d) => d.toLowerCase().includes(query));
    }
  };


  return (
    <div class="content">
      <Row>
          <Col md="1">
                  <Row>
                  <AddNewUrl/>
                  </Row>
            </Col>
            <Col md="7">
                  <Row>
                  <Input
                          value={searchQuery}
                          type="text"
                          label="Enter a city name"
                          variant="outlined"
                          placeholder="Search..."
                            onInput={(e) => {
                      setSearchQuery(e.target.value);
                    }}
                 />
                 </Row>
             </Col>    
              <Col md="1">
                 <Row>
                 <IconButton type="submit" aria-label="search">
                    <SearchIcon style={{ fill: "blue" }} />
                  </IconButton>
                  </Row>
                </Col>
             </Row> 
      <Row>
        <Col md="3"></Col>
        <Col md="6">
          <Row>
              <FormControl component="fieldset">
                <FormGroup aria-label="position" row>
                  <FormControlLabel
                    value="top"
                    control={<Checkbox />}
                    label="My Links"
                    labelPlacement="right"
                  />
                  <FormControlLabel
                    value="start"
                    control={<Checkbox />}
                    label="Public"
                    labelPlacement="right"
                  />
                  <FormControlLabel
                    value="bottom"
                    control={<Checkbox />}
                    label="Following"
                    labelPlacement="right"
                  />
                </FormGroup>
            </FormControl>
          </Row>
        </Col>
        <Col md="3"></Col>
      </Row>
          
     <div>
    
    </div>   
    <div>
     
    </div>   
    <div>   
    <Box sx={{ height: 400, width: '100%' }}>
      <DataGrid
        rows={searchedData}
        columns={columns}
        pageSize={5}
        rowsPerPageOptions={[5]}
        disableSelectionOnClick
        experimentalFeatures={{ newEditingApi: true }}
      />
    </Box>
    </div>   
    </div>
  );
}

export default Home;
